import { UrlImage } from "../GlobalComponents/Img";
export function returnImg(imgUrl, type = null) {
    if (type != null) {
        return 'https://www.gda-services.com/static/media/logo.e6ec39ca.png';
    } else {
        if (['https://www.gda-services.com/public/candidats/image/',
            'https://www.gda-services.com/public/pages/'].includes(imgUrl)) {
            return 'https://raw.githubusercontent.com/EvansUchwa/Evans-img/main/defaultUserPic.png'
        } else {
            return imgUrl;
        }
    }
}


export function getImgOnTxt(text) {
    var urlImgRegex = /(https?:\/\/.*\.(?:png|jpg|svg))/i;
    if (urlImgRegex.test(text)) {
        return <UrlImage props={{ src: text, alt: 'Image on text preview' }} />
    } else if (text == 'https://www.gda-services.com/public/candidats/image/') {
        return <p>Aucun Fichier</p>
    }
    else {
        return <p>{text}</p>
    }
}