import { useParams } from "react-router-dom";
import { UpdateProfilForm } from "../RoutesSubComponents/modifier-Profil";
import { UpdateJobForm } from "../RoutesSubComponents/modifier-Job.js";

import { UpdatePasswordForm } from "../RoutesSubComponents/modifier-Motdepasse";
const Modifier = () => {
    const params = useParams();
    const { cible, cibleId, hash } = params;
    return <>
        <div className="updateInfoPart">

            {
                (() => {
                    if (cible) {
                        if (cible && cibleId) {
                            if (cible === 'job') {
                                return <>
                                    <h1>Modifier votre offre d'emploi</h1>
                                    <UpdateJobForm props={{ jobId: cibleId }} />
                                </>
                            }

                        } else if (cible && !cibleId) {
                            return <>
                                <h1>Modifier votre {cible} </h1>
                                <UpdateProfilForm />
                            </>
                        }
                    } else if (hash) {
                        return <>
                            <h1>Veuillez entrez votre nouveau mot de passe</h1>
                            <UpdatePasswordForm props={{ hash }} />
                        </>
                    }
                })()
            }
        </div>
    </>
}

export default Modifier;