import { useParams } from 'react-router-dom';
import '../Assets/styles/accueil.css'
import {
    AccueilBanner, Partenaires, TypeServices,
    AnnonceListe, WhyUs
} from '../RoutesSubComponents/accueil';

const Accueil = () => {
    const { homeType } = useParams();
    function dispatchHomePAgeComponents(homeType) {
        if (!homeType || homeType == '') {
            return <>
                <WhyUs />
                <Partenaires />
                <TypeServices />
            </>
        } else if (['Immobilier', 'Boutique'].includes(homeType)) {
            return <>
                <AnnonceListe props={{ homeType: homeType ? homeType : '' }} />
            </>
        }
    }
    return <div className='accueil'>
        <AccueilBanner props={{ homeType: homeType ? homeType : '' }} />
        {dispatchHomePAgeComponents(homeType)}
    </div>
}

export default Accueil;