import axios from "axios";
import { useEffect, useState } from "react";
import { useAuth } from "../hooks/authHooks";
import { formationFields, serviceFields } from "../RawData/fields";
import { adminElementKey } from "../RawData/key";
import { dispatchFormData, handleUserFieldInfoChange } from "../Tools/formFunction";
import { dispatchInputOrSelect } from "../Tools/formValidator";
import { getImgOnTxt } from "../Tools/imgs";
import { SimpleIconLoader } from "./Loader";
import { Modal } from "./Modal";
export const Table1 = ({ props }) => {
    const { subTarget, rowData, setCurrentItem, dataTitle, twoDataKey } = props
    const [finalTab, setFinalTab] = useState();
    const [toggleModal, setToggleModal] = useState(false);
    const [modalComponent, setModalComponent] = useState('');

    function dispatchActionBtn(target, info) {
        const tab = [];
        if (['candidat', 'entreprise'].includes(target)) {
            tab.push({ className: 'detailBtn', id: '', ph: 'Detail', modalComponent: 'detail' })
            if (info.est_publier === "0" || info.is_active == '0') {
                tab.push({ className: 'disableBtn', id: '', ph: 'Inactif', modalComponent: 'enable' })
            } else {
                tab.push({ className: 'enableBtn', id: '', ph: 'Actif', modalComponent: 'disable' })
            }
            tab.push({ className: 'deleteBtn', id: '', ph: 'Supprimer', modalComponent: 'delete' })
        } else {
            tab.push({ className: 'detailBtn', id: '', ph: 'Detail', modalComponent: 'detail' },
                { className: 'updateBtn', id: '', ph: 'Modifier', modalComponent: 'update' },
                { className: 'deleteBtn', id: '', ph: 'Supprimer', modalComponent: 'delete' })
        }

        return tab;
    }
    useEffect(() => {
        setFinalTab(props.rowData)
    }, [props])

    function dispatchAdminModal(action, data, targetIndex) {
        let props1 = {
            action,
            setToggleModal,
            targetIndex,
            targetData: data, target: subTarget,
            finalTab, setFinalTab
        }
        if (action == 'update') {
            return <AdminUpdateModal props={props1} />
        } else if (action == 'delete') {
            return <AdminDeleteModal props={props1} />
        } else if (['enable', 'disable'].includes(action)) {
            return <AdminEnableOrDisableModal props={props1} />
        }
        else if (action == 'detail') {
            return <AdminDetailModal props={{ data, target: subTarget }} />
        }
    }

    return <><SearchOnTable props={{ target: subTarget, data: rowData, tabSetter: setFinalTab }} />
        <table className="table1">
            <thead>
                <tr>
                    <th>#</th>
                    <th>{dataTitle[0].title}</th>
                    <th>{dataTitle[1].title}</th>
                    <th>Action</th>
                </tr>
            </thead>
            <tbody>
                {
                    props.rowData.length
                        ? finalTab.map((row, index) => <tr key={'table1' + index}>
                            <td><span>{index + 1}</span></td>
                            <td><span>{getImgOnTxt(row[twoDataKey[0].key])}</span> </td>
                            <td><span>{getImgOnTxt(row[twoDataKey[1].key])} </span></td>
                            <td>
                                {
                                    dispatchActionBtn(subTarget, row).map((btn, btnIndex) => <button className={btn.className}
                                        key={'btn-Table' + btnIndex}
                                        onClick={() => {
                                            setToggleModal(true)
                                            setCurrentItem(row)
                                            setModalComponent(dispatchAdminModal(btn.modalComponent, row, index))
                                        }}>{btn.ph}</button>)
                                }
                            </td>
                        </tr>)
                        : <SimpleIconLoader />
                }
            </tbody>
        </table>
        {
            toggleModal ?
                <Modal props={{ content: modalComponent, setToggleModal, className: ' ' }} />
                : ''
        }
    </>
}



const SearchOnTable = ({ props }) => {
    const { target, data, tabSetter } = props;
    const handleSearchOnTab = (event) => {
        const value = event.target.value.toLowerCase();
        const copyData = data.slice();

        let result = ''
        if (target == 'formation') {
            result = copyData.filter(item => item.titre.includes(value))
        } else if (target == 'service') {
            result = copyData.filter(item => item.libelle.includes(value))
        }
        else if (target == 'candidat') {
            result = copyData.filter(item => item.prenom.toLowerCase().includes(value)
                || item.nom.toLowerCase().includes(value))
        }
        else if (target == 'entreprise') {
            result = copyData.filter(item => item.nom_entreprise.toLowerCase().includes(value))
        }

        if (result.length > 0) {
            tabSetter(result)
        } else {
            tabSetter([])
        }
    }
    return <div className="searchOnTable">
        <input className="semiRounded" type={"search"}
            placeholder="Rechercher ici"
            onChange={(event) => handleSearchOnTab(event)} />
    </div>
}

export const AdminUpdateModal = ({ props }) => {
    const { apiInfos } = useAuth();
    const { baseApi, headerApi } = apiInfos;
    const { setToggleModal, target, targetIndex,
        targetData, finalTab, setFinalTab } = props
    const dispatchFormFields = (type) => {
        if (type == 'formation') {
            return [formationFields.titre, formationFields.inscription,
            formationFields.prix, formationFields.description, formationFields.duree]
        } else if (type == 'service') {
            return [serviceFields.libelle, serviceFields.image]
        }
    }
    const [fields, setFields] = useState(dispatchFormFields(target));

    const [formValues, setFormvalue] = useState({
        titre: targetData.titre,
        inscription: targetData.frais_inscription,
        prix: targetData.prix,
        description: targetData.description,
        duree: targetData.duree,

        image: targetData.image_url,
        libelle: targetData.libelle
    })
    const [errors, setErrors] = useState([]);

    function handleSubmit(event, target) {
        event.preventDefault();
        let apiUrl = '';
        let formData = new FormData();
        if (target == 'formation') {
            apiUrl = "/api/form/formation/update/" + targetData.id
        } else if (target == 'service') {
            apiUrl = "/api/serv/service/update/" + targetData.id
        }

        dispatchFormData(formData, adminElementKey[target], formValues)

        axios.post(baseApi + apiUrl, formData, { headers: headerApi })
            .then(res => {
                if (res.data.id) {
                    finalTab[targetIndex] = res.data;
                    setFinalTab(finalTab)
                }
                setToggleModal(false)
            })
            .catch(err => console.log(err))

    }
    return <>
        <form onSubmit={(event) => handleSubmit(event, target)}>
            {
                fields.map((field, index) => <div className="formSegment"
                    key={"admin update " + target + "info field" + index}>
                    <label>{field.label} </label>
                    {dispatchInputOrSelect(formValues, setFormvalue,
                        field, handleUserFieldInfoChange,
                        errors, setErrors)}
                </div>)
            }
            <div className="formBtn">
                <button>Sauvegarder</button>
            </div>
        </form>
    </>
}

export const AdminDetailModal = ({ props }) => {
    // Object.keys(props.data).map((info, index) => {

    // const detailKey = ['formation', 'service'].includes(props.target)
    //     ? userDetailModalKey[props.target.toLowerCase()] : userDataKey[props.target.toLowerCase()];

    return <>
        {
            adminElementKey[props.target.toLowerCase()].map((info, index) => {
                return <div className="adminDetailModal" key={'admin modal' + index}>
                    {
                        !['id', 'created_at', 'updated_at'].includes(info) ?
                            <>
                                <b>{info.title} </b>
                                <br />
                                <span>{getImgOnTxt(props.data[info.key])} </span>
                            </> : ''
                    }
                </div>
            })
        }
    </>
}

export const AdminEnableOrDisableModal = ({ props }) => {
    const { apiInfos } = useAuth();
    const { baseApi, headerApi } = apiInfos;
    const { action, setToggleModal,
        target, targetIndex, targetData,
        finalTab, setFinalTab } = props

    function confirmEnableOrDisable(target) {

        const urlAction = action === 'enable' ? 'activer' : 'desactiver'
        const bodyKey = target + '_id';
        const apiBody = { [bodyKey]: targetData.id }
        let targetOnUrl = '';
        if (target == 'candidat') {
            targetOnUrl = 'candidats';
        } else {
            targetOnUrl = 'entreprise';
        }
        let apiUrl = "/api/" + targetOnUrl + "/" + urlAction


        axios.post(baseApi + apiUrl, apiBody, { headers: headerApi })
            .then(res => {
                if (target == 'candidat') {
                    finalTab[targetIndex].est_publier = action == 'enable' ? "1" : "0"
                } else {
                    finalTab[targetIndex].is_active = action == 'enable' ? "1" : "0"
                }
                setFinalTab(finalTab)
                setToggleModal(false)
            })
            .catch(err => console.log(err))
    }
    return <div className="confirmationContainer">
        <p>Voulez vous vraiment {action == 'enable' ? 'activer' : 'desactiver'} cet element ?</p>
        <section >
            <button className="enableBtn" onClick={(event) => confirmEnableOrDisable(target)}>Oui</button>
            <button className="deleteBtn" onClick={(event) => setToggleModal(false)}>Non</button>
        </section>
    </div>
}


export const AdminDeleteModal = ({ props }) => {
    const { apiInfos } = useAuth();
    const { baseApi, headerApi } = apiInfos;
    const { setToggleModal, target, targetIndex, targetData, finalTab, setFinalTab } = props

    function confirmDelete(target) {
        let apiUrl = '';
        if (target == 'formation') {
            apiUrl = "/api/form/formation/" + targetData.id
        } else if (target == 'service') {
            apiUrl = "/api/serv/service/" + targetData.id
        } else if (target == 'candidat') {
            apiUrl = "/api/candidats/delete/" + targetData.id
        } else if (target == 'entreprise') {
            apiUrl = "/api/entreprise/delete/" + targetData.id
        }

        axios.delete(baseApi + apiUrl, { headers: headerApi })
            .then(res => {
                finalTab.splice(targetIndex, 1)
                setFinalTab(finalTab)
                setToggleModal(false)

            })
            .catch(err => console.log(err))
    }
    return < div className="confirmationContainer">
        <p>Voulez vous vraiment supprimer cet element ?</p>
        <section>
            <button className="enableBtn" onClick={(event) => confirmDelete(target)}>Oui</button>
            <button className="deleteBtn" onClick={(event) => setToggleModal(false)}>Non</button>
        </section>
    </div>
}