import '../Assets/styles/dashboard.css'

import {
    DashWelcomeBanner, DashboardFastLinks,
    DashboardStats, AccountNotActivated, EnterpriseList, DashEnterpriseContract
} from "../RoutesSubComponents/dashboard";
import { useAuth } from '../hooks/authHooks';

const Dashboard = () => {
    const { authedInfo, userType, accountIsActivated } = useAuth();
    const { name } = authedInfo.general;
    const userDashboardComponents = {
        ENTREPRISE: <>
            <DashboardFastLinks props={{ userType }} />
        </>,
        CANDIDAT: <>
            <DashboardFastLinks props={{ userType }} />
            <EnterpriseList />
        </>
    }

    return <>
        {
            userType == 'ENTREPRISE' ? <DashEnterpriseContract /> : ''
        }
        <DashWelcomeBanner props={{ userType, name }} />
        <DashboardStats props={{ userType }} />
        {userDashboardComponents[userType]}
        {accountIsActivated() ? '' : <AccountNotActivated />}
    </>

}

export default Dashboard;
