import axios from "axios";

export const formStepsValidator = (userType, formValues, stepId) => {
    const {
        nom, prenom,
        sexe, date_de_naissance, lieu_de_naissance,
        poste_envisagé, salaire_envisagé,
        niveau_detude, filière_serie, ecole,
        pays, adresse, nationalité,
        moyen_de_deplacement, situation_matrimoniale,
        domaine_de_competence, autre_competence,

        nom_entreprise, mail_entreprise,
        type_entreprise, logo_entreprise,
        ifu, localisation_entreprise,
        description_entreprise,

        nom_boutique, type_commerce,
        localisation_boutique,
        description_boutique,

        profil, photo_1, photo_2,
        numero_1, numero_2,
    } = formValues


    if (userType == 'CANDIDAT') {
        if (stepId == 1) {
            return nom != "" && prenom != "" && sexe != ""
                && date_de_naissance != "" && lieu_de_naissance != ""
                && numero_1 != "" && numero_2 != "";
        }
        else if (stepId == 2) {
            return niveau_detude != "" && filière_serie != "" && ecole != ""
                && pays != "" && adresse != "" && nationalité != ""
                && moyen_de_deplacement != "" && situation_matrimoniale != "";
        }
        else if (stepId == 3) {
            return poste_envisagé != "" && salaire_envisagé != ""
                && domaine_de_competence != "" && autre_competence != ""
                && (photo_1 != "" || photo_2 != "");
        }
    } else if (userType == 'ENTREPRISE') {
        if (stepId == 1) {
            return nom_entreprise != "" && mail_entreprise != "" && ifu != "";
        } else if (stepId == 2) {
            return localisation_entreprise != "" && numero_1 != "" && numero_2 != "";
        }
        else if (stepId == 3) {
            return type_entreprise != "" && logo_entreprise != "" && description_entreprise != "";
        }

    } else if (userType == 'BOUTIQUE') {

        if (stepId == 1) {
            return nom_boutique != "" && type_commerce != "";
        } else if (stepId == 2) {
            return localisation_boutique != "" && numero_1 != "";
        }
        else if (stepId == 3) {
            return profil != "" && description_boutique != "";
        }
    }
}

export const formStepSubmiter = (apiInfos, userType, formValues, updateOtherUserInfo, userMail, userId) => {
    const { baseApi, headerApi } = apiInfos;
    let apiUrlEnd = '';
    const formData = new FormData()
    const {
        nom, prenom,
        sexe, date_de_naissance, lieu_de_naissance,
        poste_envisagé, salaire_envisagé,
        niveau_detude, filière_serie, ecole,
        pays, adresse, nationalité,
        moyen_de_deplacement, situation_matrimoniale,
        domaine_de_competence, autre_competence,

        nom_entreprise, mail_entreprise,
        type_entreprise, logo_entreprise,
        ifu, localisation_entreprise,
        description_entreprise,

        nom_boutique, type_commerce,
        localisation_boutique,
        description_boutique,

        profil, photo_1, photo_2,
        numero_1, numero_2,
    } = formValues


    if (userType == 'CANDIDAT') {

        apiUrlEnd = '/api/auth/register/candidat/sept'
        formData.append("email", userMail)
        formData.append("nom", nom)
        formData.append("prenom", prenom)
        formData.append("sexe", sexe)
        formData.append("date_naissance", date_de_naissance)
        formData.append("lieu", lieu_de_naissance)
        formData.append("tel", numero_1)
        formData.append("tel2", numero_2)
        formData.append("adresse", adresse)
        formData.append("niveau", niveau_detude)
        formData.append("situation_matrimonial", situation_matrimoniale)
        formData.append("filiere", filière_serie)
        formData.append("ecole", ecole)
        formData.append("competences", domaine_de_competence)
        formData.append("autre_competence", autre_competence)
        formData.append("experience", "Une experience")
        formData.append("poste_envisager", poste_envisagé)
        formData.append("moyens_de_deplacement", moyen_de_deplacement)
        formData.append("pretention_salarials", salaire_envisagé)
        formData.append("pays", pays)
        formData.append("nationality", nationalité)
        formData.append("photo1", photo_1)
        formData.append("photo2", photo_2)
    }
    else if (userType == 'ENTREPRISE') {
        formData.append("email", userMail)
        formData.append("email_entrepise", mail_entreprise)
        formData.append("nom_entreprise", nom_entreprise)
        formData.append("ifu", ifu)
        formData.append("contact", numero_1)
        formData.append("contact2", numero_2)
        formData.append("niveau", niveau_detude)
        formData.append("type", type_entreprise)
        formData.append("situation_geographie", localisation_entreprise)
        formData.append("description", description_entreprise)
        formData.append("logo", logo_entreprise)

        apiUrlEnd = '/api/auth/register/entreprise/sept'
    }
    else if (userType == 'BOUTIQUE') {
        formData.append("email", userMail)
        formData.append("nom", nom_boutique)
        formData.append("contact", numero_1)
        formData.append("situation_geographie", localisation_boutique)
        formData.append("type", type_commerce)
        formData.append("description", description_boutique)
        formData.append("photo_boutique", profil)
        formData.append("user_id", userId);

        apiUrlEnd = "/api/bout/boutique"
    }


    axios.post(baseApi + apiUrlEnd, formData, { headers: headerApi })
        .then(res => {
            if (res.data.data) {
                updateOtherUserInfo(res.data.data)
            } else {
                updateOtherUserInfo(res.data)
            }
        })
        .catch(err => console.log(err))
}