import { handleUserFieldInfoChange } from '../Tools/formFunction';
// import { CurrentStep } from '../GlobalComponents/Steps';
import { useState } from 'react'
import { getError } from '../Tools/formValidator';
import { dispatchInputOrSelect } from '../Tools/formValidator';
import { useAuth } from '../hooks/authHooks';
import { formStepSubmiter, formStepsValidator } from '../helpers/accountFormSteps';
import { allTypeSteps } from '../RawData/steps';
import { useNavigate } from 'react-router';



export const Stepper = ({ props }) => {
    const { currentStep } = props
    const dispatchNumberSteps = () => {
        return [0, 1, 2]
    }

    const steps = dispatchNumberSteps();

    return <section className='stepper'>
        {
            steps.map((stp, index) => <span
                className={(currentStep - 1) >= index ? 'rounded activeStep' : 'rounded'}
                key={index + 'step'}>{index + 1}</span>)
        }
    </section>
}

export const StepForm = ({ props }) => {
    const { currentStep, setCurrentStep, userType } = props;
    const { authedInfo, updateOtherUserInfo, apiInfos } = useAuth();

    const [errors, setErrors] = useState([]);
    const allUserOtherFields = {
        'CANDIDAT': {
            nom: '', prenom: '',
            sexe: '', date_de_naissance: '', lieu_de_naissance: '',
            poste_envisagé: '', salaire_envisagé: '',
            numero_1: '', numero_2: '',
            niveau_detude: '', filière_serie: '', ecole: '',
            pays: '', adresse: '', nationalité: '',
            moyen_de_deplacement: '', situation_matrimoniale: '',
            domaine_de_competence: '', autre_competence: '',
            photo_1: '', photo_2: '', profil: ''
        },
        'ENTREPRISE': {
            nom_entreprise: '', mail_entreprise: '',
            type_entreprise: '', logo_entreprise: '',
            ifu: '', localisation_entreprise: '',
            description_entreprise: ''
        },
        'BOUTIQUE': {
            nom_boutique: '', type_commerce: '',
            localisation_boutique: '',
            numero_1: "",
            description_boutique: '',
            profil: ''
        },
    }
    const [userOtherInfo, setUserOtherInfo] = useState(allUserOtherFields[userType]);
    const navigate = useNavigate();

    // useEffect(() => {
    //     setUserOtherInfo(allUserOtherFields['BOUTIQUE'])
    // }, [props.userType])
    const validForm = (stepId) => {
        var cond = "";
        // if (userType == 'candidat') {
        //     cond = stepCandidateValidator(stepId, userOtherInfo)
        // } else if (userType == 'entreprise') {
        //     cond = stepEmployerValidator(stepId, userOtherInfo)
        // }
        cond = formStepsValidator(userType, userOtherInfo, stepId);
        if (cond && errors.length <= 0) {
            return <button className='semiRounded'>Continuer</button>
        } else {
            return <button disabled className='formBtnDisable semiRounded'>Non</button>
        }
    }

    const handleChange = (event) => {
        handleUserFieldInfoChange(event, userOtherInfo, setUserOtherInfo, errors, setErrors)
    }

    const handleSubmit = (event, currentStep) => {
        const userMail = authedInfo.general.email;
        const userRankId = authedInfo.general.id;
        event.preventDefault();
        if (currentStep == 3) {
            // formStepSubmiter = (apiInfos, userType, formValues, updateOtherUserInfo, userId)
            formStepSubmiter(apiInfos, userType, userOtherInfo, updateOtherUserInfo, userMail, userRankId)
        }
        if (currentStep == 3) {
            setTimeout(() => {
                // window.location = '/'
                navigate('/', {})
            }, 5000)
        } else {
            setCurrentStep(currentStep + 1)
        }
    }


    return <div className='finaliseSignStepsForm'>
        <section>
            <form encType="multipart/form-data" method='post'
                onSubmit={(event) => handleSubmit(event, currentStep)}>
                {
                    allTypeSteps[userType]['step' + currentStep].map((stepField, index) => <div key={"formInput+" + index}
                        className='formSegment'>
                        <label>{stepField.label} </label>
                        {
                            dispatchInputOrSelect(userOtherInfo, setUserOtherInfo,
                                stepField, handleChange, errors, setErrors)
                        }
                        {getError(stepField.name, errors)}
                    </div>)
                }
                <div className="formBtn">
                    {validForm(currentStep)}
                </div>
            </form>
            {
                userType == 'ENTREPRISE' ?
                    <div>
                        {
                            currentStep == 3 ? <label htmlFor='hlgc'>
                                <b>En cochant cette case vous activez le menu
                                    "Logiciel de gestion d'entreprise"
                                    dans votre dashboard .</b>
                                <input type="checkbox" name='haveLogiciel' id='hlgc' />
                            </label> : ''}
                    </div> : ""
            }
        </section>
    </div>
}
