import { useEffect, useState } from "react"
import { Link } from "react-router-dom"
import { UrlImage } from "../GlobalComponents/Img"
import Pulse from 'react-reveal/Pulse';
import { useAuth } from "../hooks/authHooks";
import axios from 'axios'


export const DashEnterpriseContract = () => {
    return <div className="enterpriseContract">
        <a href={require("../Assets/entrepriseContrat.pdf").default}>
            Telecharger le contrat du site (A remplir)</a>
    </div>
}


export const DashWelcomeBanner = ({ props }) => {
    const { userType, name } = props
    const dashboardHelloObj = {
        CANDIDAT: { link: "/Modifier/Profil", label: "Mettre à jour mon profil" },
        ENTREPRISE: { link: "/Ajouter/offre-emploi", label: "Poster une nouvelle offre" },
        BOUTIQUE: { link: "/Ajouter/produit", label: "Poster une nouvelle offre" },
        IMMOBILIER: { link: "/Ajouter/offre-immobiliere", label: "Poster une nouvelle offre" },
        ADMIN: { link: '', label: "Affaires" }
    }
    return <div className="dash-welcomeBanner">
        <h1>Hello {name}</h1>
        <section className="dwb-candidate">
            <Link to={dashboardHelloObj[userType].link}>{dashboardHelloObj[userType].label} </Link>
        </section>
    </div>
}


export const DashboardFastLinks = ({ props }) => {
    const { userType } = props
    const fastLinksObj = {
        CANDIDAT: [
            { offerType: 'latest' },
            { offerType: 'mostViewed' },
            { offerType: 'suggestion' },
        ],
        ENTREPRISE: [
            { offerType: 'latest' },
            { offerType: 'mostViewed' },
        ],
        BOUTIQUE: [
            { offerType: 'latest' },
            { offerType: 'mostViewed' },
        ],
        IMMOBILIER: [
            { offerType: 'latest' },
            { offerType: 'mostViewed' },
        ],
        ADMIN: [
            { offerType: 'latest' },
            { offerType: 'mostViewed' },
        ]
    }
    return <div className="dash-fastLinks">
        <h2>{userType == 'ENTREPRISE' ? 'Les profils de candidats' : 'Les offres'} </h2>
        {
            fastLinksObj[userType].map((link, index) => <OfferAndCandidatePreviewSection
                key={'fast Link nb' + index}
                props={{ offerType: link.offerType, userType }} />)
        }
    </div>
}

export const DashboardStats = ({ props }) => {
    const { authedInfo, dispatchUserRoleOpponents } = useAuth();
    const { userType } = props

    const statsObj = {
        ENTREPRISE: [
            {
                number: authedInfo.other.nbre_view,
                title: dispatchUserRoleOpponents() + ' ont visité votre profil', icon: 'mdi-eye'
            },
            { number: 3, title: 'Offres postées', icon: "mdi-book" },
        ],
        CANDIDAT: [
            {
                number: authedInfo.other.nbre_view,
                title: dispatchUserRoleOpponents() + ' ont visité votre profil', icon: 'mdi-eye'
            }
        ],
        BOUTIQUE: [
            {
                number: '13',
                title: ' ont visité votre profil', icon: 'mdi-eye'
            }
        ],
        IMMOBILIER: [
            {
                number: 'Hojo',
                title: ' ont visité votre profil', icon: 'mdi-eye'
            }
        ],
        ADMIN: [
            { number: 8, title: 'Candidats inscrits', icon: "mdi-book" },
            { number: 2, title: 'Entreprises inscrites', icon: "mdi-book" },
            { number: 8, title: 'Boutiques crées', icon: "mdi-shop" },
            { number: 2, title: 'Promoteur Immobilier inscrits', icon: "mdi-home" }
        ]
    }

    return <div className="dash-stats">
        <h2>Statistiques</h2>
        {
            statsObj[userType].map((st, index) => <section className="ds-section"
                key={index + 'statType'}>
                <b>{st.number} <br /> {st.title} </b>
                <i className={"mdi " + st.icon}></i>
            </section>)
        }
    </div>
}


const OfferAndCandidatePreviewSection = ({ props }) => {
    const { offerType, userType } = props
    const dispatchOfferTitle = () => {
        if (offerType === 'latest') {
            return userType == 'ENTREPRISE' ? 'Derniers candidats inscrits' : 'Dernières offres publiées'
        }
        else if (offerType === 'mostViewed') {
            return userType == 'ENTREPRISE' ? 'Profil (candidats) les plus visités' : 'Offres les plus populaires'
        }
        else {
            return userType == 'ENTREPRISE' ? 'Candidats recommandés pour vous' : 'Offres recommandées pour vous'
        }
    }
    return <section className="df-section">
        <b>{dispatchOfferTitle()}</b>
        <Link
            to={userType === 'CANDIDAT' ? "/Offres-Emplois/" + offerType + "/1"
                : "/Candidats/" + offerType + "/1"}>
            Voir</Link>
    </section>
}


export const EnterpriseList = () => {
    const { apiInfos } = useAuth();
    const { baseApi, headerApi } = apiInfos;
    const [list, setList] = useState([]);

    useEffect(() => {
        axios.get(baseApi + "/api/entreprise/list-entreprise", { headers: headerApi })
            .then(res => setList(res.data.data))
            .catch(err => console.log(err))
    }, [])
    return <div className="dash-enterpriseList">
        <h2>Liste de nos partenaires</h2>
        <h5>Au moins 10 nouvelles entreprises nous rejoignent chaque mois</h5>
        <ul>
            <li>
                <b>Logo</b>
                <b>Nom</b>
                <b>Secteur</b>
                <b>Action</b>
            </li>
            {
                list.map((item, index) => <li key={"enterprise number" + index}>
                    <section>
                        <UrlImage props={{ src: item.logo_url, alt: 'entreprise ok' }} />
                    </section>

                    <section>
                        <p>{item.nom_entreprise}</p>
                    </section>

                    <section>
                        <p>{item.type}</p>
                    </section>

                    <section>
                        <Link to={"/Profil/entreprise/" + item.id}>Profil</Link>
                    </section>
                </li>)
            }
        </ul>
    </div>
}

export const AccountNotActivated = () => {
    const [toggleModal, setToggleModal] = useState(false);

    return <Pulse forever duration={3000}>
        <div className="accountNotActivated">
            <b>Votre compte n'est pas activé</b>
            <Link to="/Profil">Detail</Link>
        </div>
    </Pulse>

}

