import axios from "axios";
import { useEffect } from "react";
import { useState } from "react";
import { ConfirmationModal, Modal } from "../GlobalComponents/Modal";
import { Table1 } from "../GlobalComponents/Tables"
import { useAuth } from "../hooks/authHooks";
import { formationFields, serviceFields } from "../RawData/fields";
import { adminElementKey } from "../RawData/key";
import { dispatchFormData, handleUserFieldInfoChange } from "../Tools/formFunction";
import { dispatchInputOrSelect } from "../Tools/formValidator";


export const ListeComptes = ({ props }) => {
    const { typeCompte } = props;
    const tableActionBtn = <button className="table-normalBtn">Voir</button>
    return <>
        <h1>Liste de tout les comptes {typeCompte}</h1>
        <section>
            {/* <Table1 props={{ rowData: fakeUserTabs }} /> */}
        </section>
    </>
}

export const AdminDataList = ({ props }) => {

    const { apiInfos } = useAuth();
    const { baseApi, headerApi } = apiInfos;
    const { subTarget } = props;
    const [rowData, setRowData] = useState([]);
    const [currentItem, setCurrentItem] = useState(null)

    useEffect(() => {
        let apiUrl = '';
        if (subTarget === 'formation') {
            apiUrl = "/api/form/formation"
        } else if (subTarget == 'service') {
            apiUrl = "/api/serv/service"
        } else if (subTarget == 'candidat') {
            apiUrl = "/api/candidats/list"
        } else if (subTarget == 'entreprise') {
            apiUrl = "/api/entreprise/list"
        }

        axios.get(baseApi + apiUrl, { headers: headerApi })
            .then(res => {
                if (Array.isArray(res.data)) {
                    setRowData(res.data)
                } else {
                    setRowData(res.data.data)
                }
            })
            .catch(err => console.log(err))

    }, [subTarget])
    return <div>
        <h1>Liste des {subTarget}s </h1>
        <section>
            <Table1 props={{
                subTarget,
                rowData, setCurrentItem,
                dataTitle: adminElementKey[subTarget],
                twoDataKey: adminElementKey[subTarget]
            }} />
            {/* const { rowData, setCurrentItem, btnProps: listActionBtnObj, dataTitle, twoDataKey } = props */}

        </section>
    </div>
}


export const AdminAddData = ({ props }) => {
    const { apiInfos } = useAuth();
    const { baseApi, headerApi } = apiInfos;
    const { subTarget } = props;
    const [toggleModal, setToggleModal] = useState(false)

    function dispatchFields(target) {
        if (target == 'formation') {
            return [formationFields.titre, formationFields.inscription,
            formationFields.prix, formationFields.description, formationFields.duree]
        } else if (target === 'service') {
            return [serviceFields.libelle, serviceFields.image]
        }
    }
    const [targetFields, setTargetFields] = useState(dispatchFields(subTarget))
    const [formValues, setFormvalue] = useState({
        titre: '',
        inscription: '',
        prix: '',
        description: '',
        duree: '',

        image: '',
        libelle: ''
    })
    const [errors, setErrors] = useState([])

    function handleSubmit(event, target) {
        event.preventDefault();
        let apiUrl = '';
        let formData = new FormData();
        if (target == 'formation') {
            apiUrl = "/api/form/formation"
        } else if (target == 'service') {
            apiUrl = "/api/serv/service"
        }

        dispatchFormData(formData, adminElementKey[target], formValues);

        axios.post(baseApi + apiUrl, formData, { headers: headerApi })
            .then(res => {
                if (res.data.id) {
                    // finalTab, setFinalTab
                    setToggleModal(true)
                }

            })
            .catch(err => console.log(err))

    }

    return <div className="adminAddDataForm">
        <h1>Ajouter ....</h1>
        <form onSubmit={(event) => handleSubmit(event, subTarget)}>
            {targetFields.map((field, index) => <div className="formSegment"
                key={'admin add ' + subTarget + ' field' + index}>
                <label>{field.label} </label>
                {dispatchInputOrSelect(formValues, setFormvalue,
                    field, handleUserFieldInfoChange,
                    errors, setErrors)}
            </div>)}
            <div className="formBtn">
                <button>Ajouter</button>
            </div>
        </form>
        {toggleModal ? <Modal props={{
            content: 'Votre ' + subTarget + " a bien été ajouté",
            setToggleModal, className: ""
        }} /> : ''}
    </div>
}