// notVisibleOnScroll
// .nav-contact
// simpleNav
document.addEventListener('scroll', function () {
    if (document.querySelector('.bodyNotConnected')) {
        if (window.scrollY > 200) {
            document.querySelector('.nav-contact').classList.add('notVisibleOnScroll')
            document.querySelector('.simpleNav').classList.add('navOnScroll')
            document.querySelector('.wb-logo').style.display = 'none'
            document.querySelector('.wb-logo2').style.display = 'block'
        } else {
            document.querySelector('.nav-contact').classList.remove('notVisibleOnScroll')
            document.querySelector('.simpleNav').classList.remove('navOnScroll')
            document.querySelector('.wb-logo').style.display = 'block'
            document.querySelector('.wb-logo2').style.display = 'none'

        }
    }
})