import { useParams } from 'react-router-dom';
import '../Assets/styles/admin.css'
import {
    AdminDataList, AdminAddData
} from '../RoutesSubComponents/admin';

const Admin = () => {
    const params = useParams();
    const { action, bigTarget, subTarget } = params
    const userType = 'Admin'
    return <>
        <div className="dashboardPart">
            <div className='adminPart'>
                {
                    (() => {
                        let page = null;
                        if (['emplois', 'boutique', 'immobilier', 'formation', 'service'].includes(bigTarget)) {
                            if (subTarget && ['candidat', 'entreprise'].includes(subTarget)) {
                                if (['liste', 'ajout'].includes(action)) {
                                    if (action == 'liste') {
                                        return <AdminDataList props={{ subTarget }} />
                                    } else if (action == 'ajout') {
                                        return <AdminAddData props={{ subTarget }} />
                                    }
                                }
                            } else if (!subTarget && ['liste', 'ajout'].includes(action)) {
                                if (action == 'liste') {
                                    return <AdminDataList props={{ subTarget: bigTarget }} />
                                } else if (action == 'ajout') {
                                    return <AdminAddData props={{ subTarget: bigTarget }} />
                                }
                            }
                        }
                        else {
                            page = 'hoho'
                        }
                    })()
                }
            </div>
        </div>
    </>

}

export default Admin;
