import { AsideDashNav, TopDashNav, Nav } from './GlobalComponents/Nav';
import { Navigate } from "react-router-dom";
import { useAuth } from "./hooks/authHooks";
import FinaliserInscription from './Routes/FinaliserInscription';
import { Footer } from './GlobalComponents/Footer';

const NotConnectedLayout = ({ children }) => {
    return <div className='bodyNotConnected'>
        <Nav />
        {children}
    </div>
}

const ConnectedLayout = ({ children }) => {
    return <>
        <AsideDashNav />
        <TopDashNav />
        <div className='dashboardPart'>
            {children}
        </div>
    </>
}
export const RequireAuth = ({ children }) => {
    const { authed, authedInfo,userType } = useAuth();

    return <>
        {
            (()=>{
                if(authed){
                    let userTypeCond = null;
                    if(userType == 'ADMIN'){
                        userTypeCond = authedInfo.other && authedInfo.general.id
                    }else{
                         userTypeCond = authedInfo.other && authedInfo.other.user_id
                    }

                    return userTypeCond ?
                            <>
                                <AsideDashNav />
                                <TopDashNav />
                                <div className='dashboardPart'>
                                    {children}
                                </div>
                            </> : <FinaliserInscription />
                        
                }else{
                    return <Navigate to="/Authentification/Connexion" replace />
                }   
            })()
        }
    </>
}

export const NotRequiredAuth = ({ children }) => {
    const { authed } = useAuth();
    return <>
        {authed === false ?
            <NotConnectedLayout children={children} />
            : <Navigate to="/Dashboard" />}
        <Footer />
    </>
}

export const AnyoneAuth = ({ children }) => {
    const { authed, userType } = useAuth();
    return <>
        {!authed ? <NotConnectedLayout children={children} />
            : <ConnectedLayout children={children} />}

    </>
}

export const ProfilRequireAuth = ({ children }) => {
    const { authed, authedInfo } = useAuth();

    return authed && authedInfo.other != null ? { children } : <Navigate to="/Authentification/Connexion" replace />
}

export const RequireToBeEnterprise = ({ userType,children }) => {
    return <>
    {/*<Navigate to="/Dashboard" />*/}
        {
            userType == 'ENTREPRISE' ? children : <h1>Vous ne pouvez pas avoir access a cette page</h1>
        }
    </>
}

export const RequireToBeCandidate = ({ userType,children }) => {
    return <>
        {
            userType == 'CANDIDAT' ? children : <h1>Vous ne pouvez pas avoir access a cette page</h1>
        }
    </>
}




