import { addJobFormFields, addJobFormValidator } from "../RawData/addJobForm"
import { useEffect, useState } from "react";
import { useAuth } from '../hooks/authHooks';
import axios from 'axios';
import { Modal } from "../GlobalComponents/Modal";
import { IllustrationImage } from "../GlobalComponents/Img"
import { Link } from 'react-router-dom';
import { dispatchBtn, dispatchInputOrSelect } from '../Tools/formValidator'

const validForm = (userType, offerInfo, errors) => {
    var cond = false;
    cond = addJobFormValidator(offerInfo);

    if (cond && errors.length == 0) {
        return true
    } else {
        return false;
    }
}


export const UpdateJobForm = ({ props }) => {
    const { userId, userType, apiInfos } = useAuth();
    const { baseApi, headerApi } = apiInfos
    const [toggleModal, setToggleModal] = useState(false);
    const [formBtn, setFormBtn] = useState(dispatchBtn('simple', 'Enregistrer'));
    const formFields = addJobFormFields;

    const [offerInfo, setOfferInfo] = useState(
        {
            date_de_cloture: '', adresse: '',
            type_de_contrat: '', niveau_detude: '',
            moyen_de_deplacement: '', poste: '', salaire: '',
            domaine_de_competence: '', mission: '',
            profil: '', poste_a_pourvoir: '',
        });
    const [errors, setErrors] = useState([])

    useEffect(() => {
        axios.get(baseApi + "/api/offres/show-offre/" + props.jobId, { headers: headerApi })
            .then(res => {
                setOfferInfo(
                    {
                        date_de_cloture: res.data.date_cloture, adresse: res.data.adresse,
                        type_de_contrat: res.data.type_contrat, niveau_detude: res.data.niveau_etude,
                        moyen_de_deplacement: res.data.moyen_deplacement, poste: res.data.post,
                        salaire: res.data.salaire,
                        domaine_de_competence: res.data.domaine_competence,
                        mission: res.data.mission,
                        profil: res.data.profil, poste_a_pourvoir: res.data.poste_apouvoir,
                    });
            })
            .catch(err => console.log(err))

    }, [props.jobId])

    const handleChange = (event) => {
        const name = event.target.name;
        const value = event.target.value;

        setOfferInfo({ ...offerInfo, [name]: value })
    }

    const handleSubmit = (event) => {

        setFormBtn(dispatchBtn('disableAndLoad', 'Ajout en cours'))
        event.preventDefault();
        axios.post(baseApi + "/api/offres/update-offres/" + props.jobId, {
            "post": offerInfo.poste,
            "adresse": offerInfo.adresse,
            "moyen_deplacement": offerInfo.moyen_de_deplacement,
            'mission': offerInfo.mission,
            'profil': offerInfo.profil,
            'niveau_etude': offerInfo.niveau_detude,
            'salaire': offerInfo.salaire,
            'type_contrat': offerInfo.type_de_contrat,
            'domaine_competence': offerInfo.domaine_de_competence,
            'date_cloture': offerInfo.date_de_cloture,
            'entreprise_id': userId,
            'poste_apouvoir': offerInfo.poste_a_pourvoir

        },
            { headers: headerApi })
            .then(res => {
                setFormBtn(dispatchBtn('simple', 'Ajouter'))
                if (res.data.post) {
                    setToggleModal(true);
                } else {
                    setErrors('Une erreur sest produite,veuillez reesayez')
                }
            })
            .catch(err => console.log(err))
    }

    const addModal = <div className="offerAddedMsg">
        <IllustrationImage props={{
            src: 'modal/added.svg',
            alt: 'Modal added image'
        }} />
        <p>Vos modifications ont bien été enregistré !</p>
        <Link to="/Profil">Cliquez ici pour voir</Link>
    </div>



    return <div className="addOfferForm" id="addJobForm">
        <form onSubmit={(event) => handleSubmit(event)}>
            {
                formFields.map((field, index) => <div key={'add-job-field' + index}
                    className="formSegment" id={field.id}>
                    <label>{field.label} </label>
                    {
                        dispatchInputOrSelect(offerInfo, setOfferInfo,
                            field, handleChange, errors, setErrors)
                    }
                </div>)
            }
            <div className="formBtn">
                {
                    validForm(userType, offerInfo, errors) ?
                        formBtn : dispatchBtn('disable', 'Ajouter')
                }
            </div>
        </form>
        {
            toggleModal ?
                <Modal props={{
                    content: addModal, setToggleModal, className: ''
                }} />
                : ''
        }
    </div>
}