export const AccueilBanner = ({ props }) => {
    const { homeType } = props;
    const bannerText = [
        {
            typeTitle: "En quelques clics seulement trouvez: ",
            typeWords: 'Le Travail ou le Job de vos rêves',
            img: 'candidat.svg'
        },
        {
            typeTitle: "En quelques clics seulement trouvez: ",
            typeWords: 'Le Personnel ou les prestataires qu\'il vous faut',
            img: 'entreprise.svg'
        },
        // {
        //     typeTitle: "En quelques clics seulement trouvez: ",
        //     typeWords: ['Vos futurs clients',
        //         "Vos produits neufs ou d'occasions"]
        // }, {
        //     typeTitle: "En quelques clics seulement trouvez: ",
        //     typeWords: ['La parcelle selon votre budget',
        //         "L'appartement de vos rêves "]
        // }
    ]


    function dispatchAccueilBannerImg(homeType) {
        if (homeType == '') {
            return <section className='accueilBanner-img'></section>
        } else if (homeType == 'Boutique') {
            return <section className='accueilBanner2-img'></section>
        } else if (homeType == 'Immobilier') {
            return <section className='accueilBanner3-img'></section>
        }

    }

    return <div className="accueilBanner" id="kdkdj">
        <section className="accueilBanner-sliderSection">
            <Slider>
                {
                    bannerText.map((slide, index) => <div key={"home banner slide " + index}
                        className="hbs-item" >
                        <section className='hbsi-text'>
                            <h1>
                                En quelques clics trouvez :
                            </h1>
                            <span>
                                {slide.typeWords}
                            </span>
                            <Link to="/Authentification/Inscription" className='semiRounded'>S'inscrire</Link>
                        </section>
                        <section className='hbsi-img'>
                            <IllustrationImage props={{
                                src: 'bg/' + slide.img,
                                alt: 'Home banner img' + index, className: ""
                            }} />
                        </section>
                    </div>)
                }
            </Slider>
        </section>
        {/* {dispatchAccueilBannerImg(homeType)} */}
    </div>
}


